@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Poppins:wght@500;700&display=swap');

*, 
*::before,
*::after {
    margin: 0;
    box-sizing: border-box;
}

.App {
    min-height: 100vh;
    position: relative;
    font-family: 'Poppins', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
}

footer {
    font-family: 'Montserrat', sans-serif;
    font-size: 0.875rem;
    position: absolute;
    text-align: center;
    bottom: 5%;
    width: 100%;
    font-weight: 500;

    a {
        font-weight: 700;
    }
}

.button {
    text-align: center;
    color: #fff;
    background-color: var(--select-color);
    border-radius: 0.75rem;
    box-shadow: 0 2px 4px rgba(252, 168, 47, 0.4);
    font-weight: 700;
    font-size: 1.125rem;
    height: 3.5rem;
    padding: 1rem;
    margin-top: 1.5rem;
    cursor: pointer;
}