.questionLogo {
    position: absolute;
    right: 0;
    top: -15%;
}

.question {
    font-size: 1.5rem;
    color: var(--main-color);
    margin: 2rem 0;
    font-weight: 700;
}

.button__container {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.card__button {
    @include button(var(--question-color));
    grid-template-columns: 1fr 4fr;

    &.active:hover {
        background-color: var(--select-color);
        border: 2px solid var(--select-color);
        color: #fff;
    }

    &.disabled {
        cursor: not-allowed;
    }
}

.letter {
    font-size: 1.5rem;
}

.choice {
    font-size: 1.125rem;
}

.flag {
    width: 25%;
    height: auto;
}

.button--next {
    position: relative;
    align-self: flex-end;
    width: 7.25rem;
}

.right, 
.wrong {
    color: #F2F2F2;
    position: relative;
}

.right {
    background-color: var(--right-color);
    border-color: var(--right-color);
}

.wrong {
    background-color: var(--wrong-color);
    border-color: var(--wrong-color);
}

.icon {
    width: 1.25rem;
    height: auto;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 7.5%;
}
